@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Nothing+You+Could+Do&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
  background: #242424;
  
}

._RZMQZ {
  display: none;
}
._3fz7s {
  display: none;
}

h1,
h2,
h3,
h4 {
  font-family: 'Montserrat', sans-serif;
}

.warning {
  padding-left: 10px;
  border-left: 5px solid tomato;
}

.note {
  padding-left: 10px;
  border-left: 5px solid rgb(255, 175, 55);
}

.link {
  color: rgb(103, 92, 253);
}

pre {
  font-family: 'Montserrat', sans-serif;
  font-size: 16px;
}

code {
  background-color: rgb(212, 212, 212);
  padding: 5px;
}
